import { useEffect, useState } from 'react'
import { useRouter } from 'next/router'
import ItineraryCard from '../components/ItineraryCard'
import MapView from '../components/MapView.jsx'

export default function Saved() {
  const router = useRouter()
  const [items, setItems] = useState(null)
  const [selectedItinerary, setSelectedItinerary] = useState(null)
  const apiUrl = process.env.NEXT_PUBLIC_API_URL || ''

  useEffect(() => {
    const token = localStorage.getItem('access_token')
    if (!token) {
      router.replace('/login')
      return
    }

    // fetch saved itineraries; prefer cookie-based auth if backend set HttpOnly cookie
    fetch(`${apiUrl}/itineraries`, { headers: { Authorization: `Bearer ${token}` }, credentials: 'include' })
      .then(r => r.json())
      .then(data => setItems(data || []))
      .catch(() => setItems([]))
  }, [router, apiUrl])

  if (!items) return <div style={{padding:20}}>Loading…</div>

  if (!items.length) return <div style={{padding:20}}>No saved itineraries.</div>

  // If an itinerary is selected, show its details
  if (selectedItinerary) {
    // Extract places and routes for map with enhanced metadata
    const places = []
    const routes = []
    let globalOrder = 1
    const dayPlans = Array.isArray(selectedItinerary.day_plans) ? selectedItinerary.day_plans : []
    dayPlans.forEach(dp => {
      const day = dp.day || 1
      const activities = Array.isArray(dp?.activities) ? dp.activities : []
      activities.forEach(a => {
        // Backend returns 'lon', but we check for both 'lon' and 'lng'
        const placeInfo = a?.place_info || {}
        const lat = placeInfo.lat
        const lon = placeInfo.lon || placeInfo.lng
        
        if (lat != null && lon != null && typeof lat === 'number' && typeof lon === 'number' && !isNaN(lat) && !isNaN(lon)) {
          places.push({
            lat: lat,
            lng: lon,
            name: placeInfo.place_name || placeInfo.name || a.title || 'Unknown',
            title: a.title,
            day: day,
            order: globalOrder++,
            activityType: a.activity_type || a.activityType || 'other',
            time: a.time,
            duration: a.duration_minutes || a.duration,
            description: a.description
          })
        }
        
        // Backend uses 'route_geojson' not 'route'
        const travel = a?.travel_from_previous || {}
        const routeGeometry = travel.route_geojson || travel.route
        if (routeGeometry) {
          routes.push({
            geometry: routeGeometry,
            day: day
          })
        }
      })
    })

    return (
      <div style={{padding:20, maxWidth:900, margin:'0 auto'}}>
        <button onClick={() => setSelectedItinerary(null)} style={{marginBottom:20}}>← Back to List</button>
        <ItineraryCard itinerary={selectedItinerary} />
        <div style={{marginTop:20}}>
          <MapView places={places} routes={routes} height={400} />
        </div>
      </div>
    )
  }

  return (
    <div style={{padding:20, maxWidth:900, margin:'0 auto'}}>
      <h1>Saved Itineraries</h1>
      <div style={{display:'flex', flexDirection:'column', gap:12}}>
        {items.map(it => (
          <div 
            key={it.id || it._id}
            onClick={() => setSelectedItinerary(it)}
            style={{
              border: '1px solid #ddd',
              borderRadius: 8,
              padding: 16,
              cursor: 'pointer',
              transition: 'all 0.2s',
              backgroundColor: '#fff'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = '#f5f5f5'
              e.currentTarget.style.borderColor = '#007bff'
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = '#fff'
              e.currentTarget.style.borderColor = '#ddd'
            }}
          >
            <h3 style={{margin:0, marginBottom:8}}>{it.title || 'Untitled Itinerary'}</h3>
            <p style={{margin:0, color:'#666', marginBottom:4}}>{it.destination || 'No destination specified'}</p>
            {it.description && (
              <p style={{margin:0, color:'#888', fontSize:'0.9em'}}>{it.description.substring(0, 100)}{it.description.length > 100 ? '...' : ''}</p>
            )}
            {it.created_at && (
              <p style={{margin:0, marginTop:8, fontSize:'0.85em', color:'#999'}}>
                Created: {new Date(it.created_at).toLocaleDateString()}
              </p>
            )}
            <p style={{margin:0, marginTop:4, fontSize:'0.85em', color:'#999'}}>
              Click to view details →
            </p>
          </div>
        ))}
      </div>
    </div>
  )
}
